<template>
  <div class="templatePanelRoot">
    <div
      :key="index" v-for="(template, index) in templateList"
      :class="['item', {checked: template.code === $props.defaultValue}]"
      @click="handleItemClick(template)"
    >
      <div
        :class="['img']"
        :style="{
          // eslint-disable-next-line max-len
          backgroundImage: `url(/__cbdui/api/image?type=${template.place}&code=${template.code}&image=${template.previewImg}&t=${template.random})`
        }"></div>
      <div class="text">{{template.name}}</div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    defaultValue: String,
    type: String,
  },
  mounted() {
  },
  data() {
    return {};
  },
  computed: {
    templateList() {
      const { newTemplate } = this.$store.state;
      return newTemplate.templates.filter((template) => template.type === this.$props.type);
    },
  },
  methods: {
    handleItemClick(template) {
      this.$emit('onChange', template.code, template);
    },
  },
};
</script>
<style lang="less" scoped>
  .templatePanelRoot {
    display: flex;
    flex-wrap: wrap;
    padding: 10px;
  }

  .item {
    width: 220px;
    padding: 5px;
    margin: 5px;
    cursor: pointer;
    box-shadow: 0 0 0 transparent;
    transition: box-shadow 300ms;


    &:hover {
      box-shadow: 1px 1px 15px #cccccc;
    }

    &.checked {
      border: 1px solid #1199FF;
      color: #1199FF;
    }
  }

  .img {
    height: 133px;
    border: 1px solid #EEEEEE;
    background-size: contain;
  }

  .text {
    margin-top: 5px;
  }
</style>
