<template>
  <div class="root vFlex">
    <div class="hFlex" style="flex: 1; overflow: hidden;">
      <div class="leftMenu vFlex">
        <GhostButton @click="$router.push('/')" />
        <Menu class="menu"
              :default-active="activeMenu"
              @select="handleMenuSelect"
              width="180">
          <MenuItem index="page">
            <Icon class="el-icon-document-add"></Icon>
            页面
          </MenuItem>
          <MenuItem index="component">
            <Icon class="el-icon-s-grid"></Icon>
            组件
          </MenuItem>
          <MenuItem index="model">
            <Icon class="el-icon-coin"></Icon>
            Model
          </MenuItem>
        </Menu>
        <Tooltip content="同步模板">
          <GhostButton class="button" @click="syncTemplates">
            <Icon class="el-icon-refresh" />
          </GhostButton>
        </Tooltip>
      </div>

      <div class="content vFlex">
        <div class="row title">{{pageTitle}}</div>
        <div class="row hFlex" style="flex-wrap: wrap;">
          <span class="label">请输入名称:</span>
          <Input class="input" v-model="name" :clearable="true" size="mini"
                 @keyup.native.enter="createFromTemplate" />
          <div style="margin: 0 10px 0 15px; white-space: nowrap; user-select: contain;">
            当前模板：【{{selectedTemplateName}}】
          </div>
          <Button size="mini" @click="showTemplateDrawer = true">切换模板</Button>
        </div>
        <div style="border-top: 1px solid #EEEEEE;" />
        <div class="treeContent">
          <div style="overflow: auto; height: 100%;">
            <Tree
              ref="tree"
              node-key="id"
              :data="treeData"
              :default-expanded-keys="defaultExpandedKeys"
              :render-content="renderTreeContent"
              :expand-on-click-node="false"
              :highlight-current="true"
              @current-change="handleCurrentChange"
              @node-expand="handleTreeNodeExpand"
              @node-collapse="handleTreeNodeCollapse"
            />
          </div>
          <div class="treeButtonsGroup hFlex">
            <Tooltip content="显示文件">
              <div class="treeIcon" @click="showFile = !showFile">
                <Icon class="el-icon-document" :style="{color:showFile ? '#1199FF' : ''}" />
              </div>
            </Tooltip>
            <Tooltip content="刷新列表">
              <div class="treeIcon" @click="getFileTree">
                <Icon class="el-icon-refresh" />
              </div>
            </Tooltip>

          </div>
        </div>

        <div v-if="!!warnTip" class="warnTip">{{warnTip}}</div>
        <div class="result" v-if="!!name">
          <div>生成列表：</div>
          <div class="resultItem" :key="index" v-for="(filename, index) in previewFilenames">
            {{currentNodeKey}}/<span class="previewFilename">{{filename}}</span>
          </div>
        </div>
      </div>
    </div>
    <div class="footer hFlex">
      <div style="flex:1"></div>
      <Button type="primary" size="small" @click="createFromTemplate">创建</Button>
    </div>
    <Drawer
      destroy-on-close
      :visible.sync="showTemplateDrawer"
      direction="ttb"
      title="选择模板"
      size="80%">
      <TemplatePanel
        :type="activeMenu" :defaultValue="selectedTemplate"
        @onChange="handleTemplateChange" />
    </Drawer>
  </div>
</template>

<script type="text/jsx">
import { getFileTree } from '@/services/fileServices';
import {
  previewTemplateFiles,
  syncTemplates,
  createFromTemplate,
  checkTemplatePath, mkdir,
} from '@/services/templateServices';
import checkPathEnable, { checkCreateDirEnable } from '@/views/checkPathEnable';
import GhostButton from '@/components/GhostButton.vue';
import TemplatePanel from '@/components/TemplatePanel.vue';

export default {
  name: 'NewTemplate',
  components: {
    GhostButton,
    TemplatePanel,
  },
  mounted() {
    // eslint-disable-next-line no-underscore-dangle
    window._this = this;
    this.getFileTree();
    this.$store.dispatch('newTemplate/getTemplates');
  },
  data() {
    return {
      warnTip: '',
      defaultExpandedKeys: [],
      expandedKeys: [],
      previewFilenames: [],
      name: '',
      fileTree: [],
      currentNodeKey: '/page',
      showFile: false,
      showTemplateDrawer: false,
    };
  },
  computed: {
    pageTitle() {
      const { newTemplate } = this.$store.state;

      return {
        page: '新建页面',
        component: '新建组件',
        model: '新建模板',
      }[newTemplate.activeMenu];
    },
    activeMenu() {
      const { newTemplate } = this.$store.state;
      return newTemplate.activeMenu;
    },
    treeData() {
      /* 根据大类区分需要展示的树 */
      const { newTemplate } = this.$store.state;
      const { activeMenu } = newTemplate;
      const { showFile } = this;
      const revMap = (data = [], parent = '') => data.filter((item) => !item.name.startsWith('.'))
        .filter(({ type }) => showFile || type !== 'file')
        .map((item) => ({
          ...item,
          id: `${parent}/${item.name}`,
          label: item.name,
          children: revMap(item.children, `${parent}/${item.name}`),
          disabled: item.type === 'file',
        }));
      if (activeMenu === 'page') {
        return revMap(this.fileTree.filter(({ name }) => name === 'pages'));
      }
      if (activeMenu === 'component') {
        return revMap(this.fileTree.filter(({ name }) => name === 'pages' || name === 'components'));
      }
      if (activeMenu === 'model') {
        return revMap(this.fileTree.filter(({ name }) => name === 'pages' || name === 'models'));
      }
      return revMap(this.fileTree);
    },
    selectedTemplate() {
      const { config, newTemplate } = this.$store.state;
      if (newTemplate.activeMenu === 'page' && config.templatePage) {
        return config.templatePage;
      }
      if (newTemplate.activeMenu === 'model' && config.templateModel) {
        return config.templateModel;
      }
      if (newTemplate.activeMenu === 'component' && config.templateComponent) {
        return config.templateComponent;
      }
      return newTemplate.activeMenu;
    },
    selectedTemplateName() {
      const { newTemplate } = this.$store.state;
      const { templates, activeMenu } = newTemplate;
      const template = templates.find(
        (item) => item.code === this.selectedTemplate && item.type === activeMenu,
      );
      if (template) {
        return template.name;
      }
      return '未选择';
    },
  },
  watch: {
    name() {
      this.previewTemplateFiles();
      this.checkTemplatePath();
    },
    '$store.state.newTemplate.activeMenu': function () {
      this.defaultExpandedKeys = this.expandedKeys;
    },
    fileTree() {
      const keys = this.fileTree
        .filter(({ type }) => type !== 'file')
        .map(({ name }) => `/${name}`);
      if (this.expandedKeys.length === 0) {
        this.defaultExpandedKeys = keys;
        this.expandedKeys = keys;
      } else {
        this.defaultExpandedKeys = this.expandedKeys;
      }
    },
    treeData() {
      function findId(data, key) {
        return data.some((d) => {
          if (d.id === key) {
            return true;
          }
          return findId(d.children, key);
        });
      }

      this.previewTemplateFiles();
      const found = findId(this.treeData, this.currentNodeKey);
      setTimeout(() => {
        if (!found) {
          this.currentNodeKey = this.treeData[0].id;
        }
        this.$refs.tree.setCurrentKey(this.currentNodeKey);
      });
    },
  },
  methods: {
    mkdir(dirname) {
      mkdir(dirname)
        .then((result) => {
          if (result.success) {
            this.getFileTree();
            this.handleTreeNodeExpand({ id: this.currentNodeKey });
          }
        });
    },
    createFromTemplate() {
      // const { newTemplate } = this.$store.state;
      createFromTemplate({
        name: this.name,
        path: this.currentNodeKey,
        template: this.selectedTemplate,
      })
        .then((result) => {
          if (result.success) {
            this.$message.success(`已成功创建${result.data.length}个文件`);
            this.name = '';
            this.getFileTree();
            this.handleTreeNodeExpand({ id: this.currentNodeKey });
          }
        });
    },
    checkTemplatePath() {
      const { newTemplate } = this.$store.state;
      checkTemplatePath({
        name: this.name,
        path: this.currentNodeKey,
        template: newTemplate.activeMenu,
      })
        .then((result) => {
          if (result.success) {
            this.warnTip = result.data;
          }
        });
    },
    getFileTree() {
      getFileTree()
        .then((result) => {
          if (result.success) {
            this.fileTree = result.data;
          }
        });
    },
    syncTemplates() {
      syncTemplates()
        .then((result) => {
          if (result.success) {
            const successTemplate = result.data.filter(({ status }) => status === 'success');
            if (successTemplate.length === 0) {
              this.$message.info('已经是最新了');
            } else {
              this.$message.success(`成功同步了模板x${successTemplate.length}【${successTemplate.map(({ name }) => name)}】`);
              this.$store.dispatch('newTemplate/getTemplates');
            }
          }
        });
    },
    previewTemplateFiles() {
      const { newTemplate } = this.$store.state;
      previewTemplateFiles({
        name: this.name,
        path: this.currentNodeKey,
        template: newTemplate.activeMenu,
      })
        .then((result) => {
          if (result.success) {
            this.previewFilenames = result.data;
          }
        });
    },
    renderTreeContent(h, { node, data }) {
      const { newTemplate: { activeMenu } } = this.$store.state;
      const enable = checkPathEnable(data.id, activeMenu);
      const dirEnable = data.type === 'directory' && checkCreateDirEnable(data.id, activeMenu);
      const folderIcon = node.expanded ? (
        <Icon class="folder-icon el-icon-folder-opened" />
      ) : (
        <Icon class="folder-icon el-icon-folder" />
      );
      return (
        <span class={`custom-tree-node ${enable ? '' : 'tree-item-disabled'}`}>
          {
            data.type === 'directory' ? (
              folderIcon
            ) : (
              <Icon class="folder-icon el-icon-document" />
            )
          }
          <span>{node.label}</span>
          <div style="flex:1"></div>
          {dirEnable && (
            <div
              class="treeItemButton"
              onClick={() => this.mkdir(`${data.id}/${activeMenu}s`)}>
              新建 {activeMenu}s 目录
            </div>
          )}
        </span>
      );
    },
    handleMenuSelect(activeMenu) {
      this.$store.commit('newTemplate/update', {
        activeMenu,
      });
      this.checkTemplatePath();
    },
    handleCurrentChange(data) {
      if (data.type !== 'file') {
        this.currentNodeKey = data.id;
        this.checkTemplatePath();
      } else {
        this.$message({
          message: '只能选择目录',
          type: 'warning',
          duration: 1000,
        });
        this.$refs.tree.setCurrentKey(this.currentNodeKey);
      }
    },
    handleTreeNodeExpand(data) {
      this.expandedKeys = [...this.defaultExpandedKeys, data.id];
    },
    handleTreeNodeCollapse(data) {
      this.expandedKeys = this.defaultExpandedKeys.filter((key) => key !== data.id);
    },
    handleTemplateChange(templateCode) {
      const { newTemplate } = this.$store.state;
      let selectedKeyName = '';
      if (newTemplate.activeMenu === 'page') {
        selectedKeyName = 'templatePage';
      }
      if (newTemplate.activeMenu === 'model') {
        selectedKeyName = 'templateModel';
      }
      if (newTemplate.activeMenu === 'component') {
        selectedKeyName = 'templateComponent';
      }
      this.$store.dispatch({
        type: 'setting/setConfig',
        [selectedKeyName]: templateCode,
      })
        .then(() => {
          this.showTemplateDrawer = false;
        });
    },
  },
};
</script>

