export default (path, type) => {
  if (type === 'model') {
    /* model文件，必须防在models目录下 */
    if (!path.endsWith('/models')) {
      return false;
    }
    if (path.includes('/components/') || path.endsWith('/components')) {
      return false;
    }
  } else if (type === 'component') {
    /* component文件必须放在components目录下 */
    if (!path.includes('/components/') && !path.endsWith('/components')) {
      return false;
    }
  } else if (type === 'page') {
    /* page文件必须放在pages目录下，且不能放在components/models下 */
    if (!path.includes('/pages/') && !path.endsWith('/pages')) {
      return false;
    }
    if (path.includes('/components/') || path.endsWith('/components')) {
      return false;
    }
    if (path.includes('/models/') || path.endsWith('/models')) {
      return false;
    }
  }
  return true;
};

export const checkCreateDirEnable = (path, type) => {
  if (type === 'page') {
    return false;
  }
  /* 新建组件/models目录，只要不是在components或models下就可以了 */
  if (path.includes('/components/') || path.endsWith('/components')) {
    return false;
  }
  if (path.includes('/models/') || path.endsWith('/models')) {
    return false;
  }
  /* 直接在pages下建立也是不推荐的，因为已经有全局的了 */
  if (path === '/pages') {
    return false;
  }
  return true;
};
